
*{
	padding:0;
	margin:0;
}
a{
	color:#333;
}
a:hover{
	text-decoration: none;
	color:#333;
}
ul{
	margin:0;
	padding:0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	list-style: none;
}
.home{
	background:#fff;
}
.empty{
	width:100%;
	height:10px;
	background:#f1f1f1;
}
.header .header-nav{
	overflow: auto;
	padding:15px 5px;
}
.header-nav li{
	float:left;
	padding:0 5px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width:20%;
}
.content{
	overflow: hidden;
	padding-bottom: 80px;
	width:100%;
}
.content .content-shop-more .seckill{
	overflow: hidden;
	padding:5px 10px;
	text-align: right;
	background:url(/static/img/home-content-shop/seckill-title.png) 10px center no-repeat;
	background-size: auto 20px;
}
.content .content-shop-more .seckill a{
	font-size: 12px;
	color:#ff5757;
}
.content .content-shop-more .seckill i{
	font-style: normal;
	font-size:16px;
}
.content .content-shop-more{
	overflow: hidden;
}
.content .content-more-img{
	position: relative;
	overflow-y: auto;
	padding-right: 10px;
	padding-bottom: 10px;
	white-space: nowrap;
	width:100%;
}
.content .content-shop-more .more{
	display: inline-block;
	-webkit-writing-mode:tb-rl;
	    -ms-writing-mode:tb-rl;
	        writing-mode:tb-rl;
	margin-bottom:18px;	
	margin-right: 8px;
	letter-spacing:3px;	
	font-size: 10px;
	color:#999;
}
.content .content-shop-more .more-arrow{
	margin-left:10px;
	margin-right: 0;
	margin-bottom:35px;
	font-size:25px;
}
.content .actcount{
	overflow: hidden;
}
.content .actcount li .actcount-meat::after{
	border-right: none;
}
.content .actcount li:nth-child(1) .actcount-meat{
	padding-bottom: 20px;
}
.content .actcount li:nth-child(2){
	float: left;
	border-bottom: 1px solid #e8e8e8;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	text-align: center;
	width:57%;
}
.content .actcount li:nth-child(2) p{
	float: left;
	width:50%;
}
.content .actcount li:nth-child(2) b{
	font-size: 13px;
}
.content .actcount li:nth-child(2) img{
	margin-top:12px;
	width:46px;
}
.content .actcount li:nth-child(3){
	border-right: 1px solid #e8e8e8;
}
.content .actcount li:nth-child(3),.content .actcount li:nth-child(4){
	float: left;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	text-align: center;
	width:28.5%;
}
.content .actcount li:nth-child(3) p,
.content .actcount li:nth-child(4) p{
	margin-bottom:0;
	padding-top:5px;
	padding-bottom:0;
}
.content .actcount li:nth-child(3) b,
.content .actcount li:nth-child(4) b{
	font-size:13px;
}
.content .actcount li:nth-child(3) img,
.content .actcount li:nth-child(4) img{
	margin-bottom: 10px;
	width:46px;
}
.content .actcount .bac{
	background:red;
}
.content .actcount .abc{
	background:pink;
}
.content .actcount li:nth-child(1){
	float: left;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	text-align: center;
	width:43%;
}
.content .actcount li:nth-child(1) .actcount-meat::after{
	border-right: 1px solid #e8e8e8;
}
.content .shop-top{
	position:relative;
	overflow: hidden;
	text-align: center;
	line-height: 50px;
	width:100%;
	background:#fff;
}
.content .shop-top span{
	position:relative;
	display: inline-block;
	padding:0 5px;
	font-weight: bold;
	font-size: 17px;
	color:#333;
	background-color: #FFF;
}
.content .shop-top::before{
	position:absolute;
	left: 50%;
	top:50%;
	content:'';
	border-top:1px solid #333;
	width:228px;
	height:1px;
	-webkit-transform: scaleY(0.5) translate(-50%,0);
	        transform: scaleY(0.5) translate(-50%,0);
}
.content .btn{
	padding:10px;
	border:none;
	outline: none;
	color:#000;
	width:100%;
	background:#f4f4f4;
}
.footer{
	overflow: hidden;
	position:fixed;
	left: 0;
	bottom:0;
	border-top:1px solid #d9d9d9;
	width:100%;
	height:55px;
	background:#fff;
}
/*.footer li{
	float:left;
	box-sizing: border-box;
	width:20%;
}*/

.top-nav-imgs{
	color:#000;
}
.top-nav-imgs:hover{
	text-decoration: none;
	color:#000;
}
.top-nav-imgs img{
	padding-bottom: 2px;
	width: 100%;
}
.top-nav-imgs span{
	display: block;
	text-align: center;
	font-size: 12px;
}

*{
	padding:0;
	margin:0;
}
.focus .chancolor{
	/*font-size:10px;*/
	color:#39ac69;
}
.posi-footer{
	display: block;
	float: left;
	padding-top:5px;
	text-align: center;
	width:20%;
}
.posi-footer a{
	font-size:10px;
	color: #888;
}
.posi-footer a:hover{
	text-decoration: none;
}
.posi-footer span{
	display: block;
	padding-top:5px;
	padding-bottom:2px;
	font-size:20px;
	color: #888;
}

.shopList{
		overflow: hidden;
		padding:15px 10px;
		border-top:1px solid #e7e9e4;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		width:100%;
}
.shopList .cleft{
		float: left;
}
.shopList .cleft .shopimg{
		margin-right: 10px;
		border:1px solid #e8e8e8;
		border-radius: 4px;
		width:66px;
}
.shopList .cleft img{
		width:100%;
}
.shopList .cright-title{
		margin:0;
		padding-bottom:5px;
		font-weight: bold;
		font-size:16px;
		color:#333;
}
.shopList .cright-title a{
		color:#333;
}
.shopList .cright-title a:hover{
		color:#333;
		text-decoration: none;
}
.shopList .cright-title img{
		float: right;
		width:15px;
}
.shopList .cright{
		overflow: hidden;
}
.shopList .star{
		overflow: hidden;
		margin-bottom: 0;
		padding:5px 0;
		font-size:10px;
}
.shopList .star span{
		position:relative;
		float: left;
		padding-right: 10px;
		color:#efa215;
}
.shopList .star b{
		padding-left: 5px;
		font-weight: normal;
		font-size:11px;
		color:#999;
}
.shopList .star span::after{
		position:absolute;
		right: 2px;
		top:4px;
		content:'';
		width:1px;
		height:10px;
		background:#999;
}
.shopList .desc{
		margin-bottom: 0;
		padding-bottom: 10px;
    border-bottom: 1px dotted #ccc;
		font-size:10px;
		color:#999;
}
.shopList .cright .squ{
		display: inline-block;
		margin-bottom: 0;
		padding:0 2px;
		border:1px solid #16A9ff;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		text-align: center;
		font-size: 10px;
		color:#16A9ff;
}
.shopList .cright .cright-bottom{
		overflow: hidden;
		margin-top:10px;
}
.shopList .cright-bottom .neck-p{
		margin-bottom:10px;
		font-size: 10px;
		color:#999;
}
.shopList .cright-bottom .neck-green i{
		background:#5FBC65;
}
.shopList .neck i{
		padding:0 2px;
		border-radius: 2px;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		font-style: normal;
		color:#fff;
		background:#FF6C57;
}
.shopList .neck .neck-ico{
		float: right;
		padding-top:2px;
}
.shopList .shop-bottom .imgs:nth-child(1){
		margin-left:0;
}
.shopList .shop-bottom .imgs{
		display: inline-block;
		position: relative;
		padding-left:10px;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		width:25%;
}
.shopList .shop-bottom .eco{
		position: absolute;
		left: 0;
		top:0;
		padding:0 2px;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		font-size: 9px;
		color:#fff;
		background:#fc2c50;
}
.shopList .shop-bottom img{
		width:100%;
}
.shopList .shop-bottom p{
		text-align: center;
		font-size:10px;
		color:red;
}

.shop-more{
	overflow: hidden;
	display: inline-block;
	margin:0 0 0 10px;
	width:20%;
}
.shop-more a:hover{
	text-decoration: none;
}
.shop-more .shop-more-img{
	position: relative;
}
.shop-more .shop-more-img span{
	display: block;
	position: absolute;
	left: 0;
	bottom:0;
	border-radius: 15px;
	text-align: center;
	font-size: 9px;
	color:#fff;
	width:100%;
	background:rgba(255,87,87,0.6);
}
.shop-more img{
	width:100%;
}
.shop-more p{
	margin-bottom: 0;
	text-align: center;
	font-size: 10px;
	color:#999;
}
.shop-more .sprice{
	font-weight: bold;
	font-size: 16px;
	color:#ff5757;
}

.actcount-meat{
	position:relative;
	overflow: hidden;
	display: block;
	color:#333;
}
.actcount-meat:hover{
	text-decoration: none;
	color:#333;
}
.actcount-meat::after{
	position: absolute;
	right: 0;
	top:0;
	content:'';
	border-right: 1px solid #e8e8e8;
	width:1px;
	height:100%;
}
.actcount-meat p{
	padding-top:20px;
	padding-bottom:20px;
}
.actcount-meat b{
	display: block;
	font-weight: bold;
	font-size:18px;
}
.actcount-meat span{
	display: block;
	font-size:10px;
	color:#999;
}
.actcount-meat img{
	width:62px;
}

.header .imgs{
	padding:0 10px;
}
.header .imgs img{
	width:100%;
}
aside{
	position:fixed;
	right: 0;
	bottom: 20%;
	z-index: 10;
	width:75px;
}
aside img{
	width:100%;
}

